@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: "Courier New", sans-serif;
}

.Card {
  @apply h-screen w-screen bg-teal-200 flex flex-col bg-cover justify-center overflow-hidden;
}

.TopContainer {
  @apply flex flex-col justify-between items-center gap-4 h-1/3;
}

.BottomContainer {
  @apply flex flex-col justify-center items-center gap-4 py-4 h-1/2;
}

.SpeechBubble {
  @apply bg-white flex w-4/6 h-1/2 rounded-lg justify-center items-center font-bold text-xl ;
}

.Buttons {
  @apply w-4/6 h-9 rounded-lg bg-slate-700 text-white text-sm;
}

.CatHead {
  @apply h-1/2;
}

.HiraganaMainContainer {
  @apply flex flex-col justify-center items-center;
}

.KanaGridContainer {
  @apply grid grid-cols-5 gap-1.5 w-5/6 py-4;
}

.KanaTitle {
  @apply flex justify-center items-center font-bold text-sm my-4;
}

.KanaGridItem {
  @apply rounded-lg bg-slate-800 text-xs text-white -order-1 flex flex-col justify-center items-center;
}

.Header {
  @apply flex justify-around items-center h-9  fixed top-0 left-0 w-screen text-xs;
}

.Footer {
  @apply flex justify-around items-center h-9 bg-white fixed bottom-0 left-0 w-screen text-xs;
}

.FinalScoreContainer {
  @apply flex flex-col justify-center items-center gap-2 text-sm;
}
